import { Directionality } from '@angular/cdk/bidi';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';

import { zoomBigMotion } from 'ng-zorro-antd/core/animation';
import { isPresetColor, NzPresetColor } from 'ng-zorro-antd/core/color';
import { NzNoAnimationDirective } from 'ng-zorro-antd/core/no-animation';
import { BooleanInput, NgStyleInterface, NzTSType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';

import {
  isTooltipEmpty,
  CmacsTooltipBaseComponent,
  NzTooltipBaseDirective,
  NzTooltipTrigger,
  PropertyMapping
} from './base';

@Directive({
  selector: '[cmacs-tooltip]',
  exportAs: 'cmacsTooltip',
  host: {
    '[class.ant-tooltip-open]': 'visible'
  }
})
export class CmacsTooltipDirective extends NzTooltipBaseDirective {
  static ngAcceptInputType_cmacsTooltipArrowPointAtCenter: BooleanInput;

  @Input('cmacsTooltipTitle') override title?: NzTSType | null;
  @Input('cmacsTooltipTitleContext') titleContext?: Object | null = null;
  @Input('cmacs-tooltip') override directiveTitle?: NzTSType | null;
  @Input('cmacsTooltipTrigger') override trigger?: NzTooltipTrigger = 'hover';
  @Input('cmacsTooltipPlacement') override placement?: string | string[] = 'top';
  @Input('cmacsTooltipOrigin') override origin?: ElementRef<HTMLElement>;
  @Input('cmacsTooltipVisible') override visible?: boolean;
  @Input('cmacsTooltipMouseEnterDelay') override mouseEnterDelay?: number;
  @Input('cmacsTooltipMouseLeaveDelay') override mouseLeaveDelay?: number;
  @Input('cmacsTooltipOverlayClassName') override overlayClassName?: string;
  @Input('cmacsTooltipOverlayStyle') override overlayStyle?: NgStyleInterface;
  @Input('cmacsTooltipArrowPointAtCenter') @InputBoolean() override arrowPointAtCenter?: boolean;
  @Input() cmacsTooltipColor?: string;

  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('cmacsTooltipVisibleChange') override readonly visibleChange = new EventEmitter<boolean>();

  override componentRef: ComponentRef<CmacsToolTipComponent> = this.hostView.createComponent(CmacsToolTipComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer, noAnimation);
  }

  protected override getProxyPropertyMap(): PropertyMapping {
    return {
      ...super.getProxyPropertyMap(),
      cmacsTooltipColor: ['cmacsColor', () => this.cmacsTooltipColor],
      cmacsTooltipTitleContext: ['cmacsTitleContext', () => this.titleContext]
    };
  }
}

@Component({
  selector: 'cmacs-tooltip',
  exportAs: 'cmacsTooltipComponent',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./cmacs-tooltip.component.css'],
  animations: [zoomBigMotion],
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      nzConnectedOverlay
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayPush]="true"
      [nzArrowPointAtCenter]="cmacsArrowPointAtCenter"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="ant-tooltip"
        [class.ant-tooltip-rtl]="dir === 'rtl'"
        [ngClass]="_classMap"
        [ngStyle]="cmacsOverlayStyle"
        [@.disabled]="!!noAnimation?.nzNoAnimation"
        [nzNoAnimation]="noAnimation?.nzNoAnimation"
        [@zoomBigMotion]="'active'"
      >
        <div class="ant-tooltip-content">
          <div class="ant-tooltip-arrow">
            <span class="ant-tooltip-arrow-content" [ngStyle]="_contentStyleMap"></span>
          </div>
          <div class="ant-tooltip-inner" [ngStyle]="_contentStyleMap">
            <ng-container *nzStringTemplateOutlet="cmacsTitle; context: cmacsTitleContext">{{ cmacsTitle }}</ng-container>
          </div>
        </div>
      </div>
    </ng-template>
  `,
  preserveWhitespaces: false
})
export class CmacsToolTipComponent extends CmacsTooltipBaseComponent {
  override cmacsTitle: NzTSType | null = null;
  cmacsTitleContext: Object | null = null;

  cmacsColor?: string | NzPresetColor;

  _contentStyleMap: NgStyleInterface = {};

  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(cdr, directionality, noAnimation);
  }

  protected isEmpty(): boolean {
    return isTooltipEmpty(this.cmacsTitle);
  }

  protected override updateStyles(): void {
    const isColorPreset = this.cmacsColor && isPresetColor(this.cmacsColor);

    this._classMap = {
      [this.cmacsOverlayClassName]: true,
      [`${this._prefix}-placement-${this.preferredPlacement}`]: true,
      [`${this._prefix}-${this.cmacsColor}`]: isColorPreset
    };

    this._contentStyleMap = {
      backgroundColor: !!this.cmacsColor && !isColorPreset ? this.cmacsColor : null
    };
  }
}
