import { Directionality } from '@angular/cdk/bidi';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';

import { zoomBigMotion } from 'ng-zorro-antd/core/animation';
import { NzConfigKey, NzConfigService, WithConfig } from 'ng-zorro-antd/core/config';
import { NzNoAnimationDirective } from 'ng-zorro-antd/core/no-animation';
import { BooleanInput, NgStyleInterface, NzTSType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { CmacsToolTipComponent } from '../cmacs-tooltip/tooltip';
import {
  isTooltipEmpty,
  NzTooltipBaseDirective,
  NzTooltipTrigger,
  PropertyMapping
} from './../cmacs-tooltip/base';

const NZ_CONFIG_MODULE_NAME: NzConfigKey = 'popover';

@Directive({
  selector: '[cmacs-popover]',
  exportAs: 'cmacsPopover',
  host: {
    '[class.ant-popover-open]': 'visible'
  }
})
export class CmacsPopoverDirective extends NzTooltipBaseDirective {
  static ngAcceptInputType_cmacsPopoverArrowPointAtCenter: BooleanInput;

  readonly _nzModuleName: NzConfigKey = NZ_CONFIG_MODULE_NAME;

  @Input('cmacsPopoverArrowPointAtCenter') @InputBoolean() override arrowPointAtCenter?: boolean;
  @Input('cmacsPopoverTitle') override title?: NzTSType;
  @Input('cmacsPopoverContent') override content?: NzTSType;
  @Input('cmacs-popover') override directiveTitle?: NzTSType | null;
  @Input('cmacsPopoverTrigger') override trigger?: NzTooltipTrigger = 'hover';
  @Input('cmacsPopoverPlacement') override placement?: string | string[] = 'top';
  @Input('cmacsPopoverOrigin') override origin?: ElementRef<HTMLElement>;
  @Input('cmacsPopoverVisible') override visible?: boolean;
  @Input('cmacsPopoverMouseEnterDelay') override mouseEnterDelay?: number;
  @Input('cmacsPopoverMouseLeaveDelay') override mouseLeaveDelay?: number;
  @Input('cmacsPopoverOverlayClassName') override overlayClassName?: string;
  @Input('cmacsPopoverOverlayStyle') override overlayStyle?: NgStyleInterface;

  @Input() @WithConfig() cmacsPopoverBackdrop?: boolean = false;

  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('cmacsPopoverVisibleChange') override readonly visibleChange = new EventEmitter<boolean>();

  override componentRef: ComponentRef<CmacsPopoverComponent> = this.hostView.createComponent(CmacsPopoverComponent);

  protected override getProxyPropertyMap(): PropertyMapping {
    return {
      cmacsPopoverBackdrop: ['cmacsBackdrop', () => this.cmacsPopoverBackdrop],
      ...super.getProxyPropertyMap()
    };
  }

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective,
    nzConfigService?: NzConfigService
  ) {
    super(elementRef, hostView, resolver, renderer, noAnimation, nzConfigService);
  }
}

@Component({
  selector: 'cmacs-popover',
  exportAs: 'cmacsPopoverComponent',
  animations: [zoomBigMotion],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-popover.component.css'],
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      nzConnectedOverlay
      [cdkConnectedOverlayHasBackdrop]="hasBackdrop"
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPush]="true"
      [nzArrowPointAtCenter]="cmacsArrowPointAtCenter"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="ant-popover"
        [class.ant-popover-rtl]="dir === 'rtl'"
        [ngClass]="_classMap"
        [ngStyle]="cmacsOverlayStyle"
        [@.disabled]="!!noAnimation?.nzNoAnimation"
        [nzNoAnimation]="noAnimation?.nzNoAnimation"
        [@zoomBigMotion]="'active'"
      >
        <div class="ant-popover-content">
          <div class="ant-popover-arrow">
            <span class="ant-popover-arrow-content"></span>
          </div>
          <div class="ant-popover-inner" role="tooltip">
            <div>
              <div class="ant-popover-title" *ngIf="cmacsTitle">
                <ng-container *nzStringTemplateOutlet="cmacsTitle">{{ cmacsTitle }}</ng-container>
              </div>
              <div class="ant-popover-inner-content">
                <ng-container *nzStringTemplateOutlet="cmacsContent">{{ cmacsContent }}</ng-container>
              </div>
            </div>
          </div>
        </div>
      </div>
    </ng-template>
  `
})
export class CmacsPopoverComponent extends CmacsToolTipComponent {
  override _prefix = 'ant-popover';

  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(cdr, directionality, noAnimation);
  }

  get hasBackdrop(): boolean {
    return this.cmacsTrigger === 'click' ? this.cmacsBackdrop : false;
  }

  protected override isEmpty(): boolean {
    return isTooltipEmpty(this.cmacsTitle) && isTooltipEmpty(this.cmacsContent);
  }
}
