import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Output,
  Input,
  ViewEncapsulation,
  OnInit,
  ChangeDetectorRef
} from '@angular/core';
import { UntypedFormControl } from "@angular/forms";

import intlTelInput from 'intl-tel-input';

@Component({
  selector: 'cmacs-phone-number',
  exportAs: 'cmacsPhoneNumber',
  templateUrl: './cmacs-phone-number.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-phone-number.component.css'],
})
export class CmacsPhoneNumberComponent implements OnInit{

  @Input() init: object;
  @Input() formControlCustom: UntypedFormControl = new UntypedFormControl('', []);
  @Output() hasError: EventEmitter<any> = new EventEmitter<any>();
  @Output() telOutput: EventEmitter<any> = new EventEmitter<any>();
  @Output() inputObject: EventEmitter<any> = new EventEmitter<any>();
  @Output() countryChange: EventEmitter<any> = new EventEmitter<any>();

  phoneInputID: string = 'phone';
  telOutputObj: any = null;

  constructor(private cdr: ChangeDetectorRef) {
    this.phoneInputID = this.uuidv4();
  }

  ngOnInit() {
    this.cdr.detectChanges();
    const input = document.getElementById(this.phoneInputID);
    if (this.init === null || this.init === undefined) {
      this.init = {
        utilsScript: "assets/build/utils.js"
      }
    }
    this.init['utilsScript'] = "assets/build/utils.js";
    this.telOutputObj = intlTelInput(input, this.init);
    this.telInputObject(this.telOutputObj);

    input.addEventListener("countrychange", () => {
      this.onCountryChange(this.telOutputObj.getSelectedCountryData());
    });
  }

  telInputObject($event){
    this.inputObject.emit($event);
  }

  onCountryChange($event){
    this.countryChange.emit($event);
    this.hasErrorEvent();
  }

  hasErrorEvent(){
    this.hasError.emit(this.telOutputObj.isValidNumber());
    this.telOutput.emit(this.telOutputObj.getNumber());
  }

  uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
      // tslint:disable-next-line: no-bitwise
      const r = Math.random() * 16 | 0;

      // tslint:disable-next-line: triple-equals
      const v = c == 'x' ?
        // tslint:disable-next-line: no-bitwise
        r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }

}
